import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);

const state = {
    loading: true,
    isLoad: false
}

const actions = { //
    success: ({ commit }) => { //
        commit('suc')
    },
    error: ({ commit }) => { //
        commit('err')
    }


}
const mutations = {
    suc(state) { //
        state.isLoad = true //
        state.loading = false //
    },
    err(state) { //
        state.isLoad = false //
        state.loading = true //
    }

}


const getters = {
    isLoad(state) {
        return state.isLoad
    },
    loading(state) {
        return state.loading
    }
}

export default new Vuex.Store({
    state,
    getters,
    mutations,
    actions
})


